.cart {
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 5;
  opacity: 0;
  background-color: #e3e3e3cc;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  flex-direction: column;
  align-self: flex-end;
  width: 450px;
  height: 750px;
  margin-top: 300px;
  transition: transform .7s, opacity .2s;
  display: flex;
  position: fixed;
  transform: translate(100%);
}

.cartOpened {
  opacity: 1;
  transform: translate(0%);
}

.cartItems {
  flex-direction: column;
  width: 450px;
  height: 570px;
  margin-top: 40px;
  display: flex;
}

.cartItem {
  background-color: none;
  border-bottom: 2px solid #000;
  width: 450px;
  height: 80px;
  margin-bottom: 1px;
  display: flex;
}

.cartItemName {
  align-self: center;
  width: 155px;
  margin: 20px 8px 20px 20px;
  font-size: 23px;
}

.cartItemQuantity::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.cartItemQuantity {
  pointer-events: none;
  text-align: center;
  background-color: #0000;
  border: none;
  align-self: center;
  width: 15px;
  margin-right: 10px;
  font-size: 25px;
}

.minusIcon, .plusIcon {
  object-fit: cover;
  cursor: pointer;
  align-self: center;
  width: 30px;
  height: 30px;
}

.cartItemPrice {
  align-self: center;
  margin-left: 15px;
  font-size: 25px;
}

.removeItemButton {
  cursor: pointer;
  align-self: center;
  width: 35px;
  height: 35px;
  position: absolute;
  right: 10px;
}

#addToCartBtn {
  cursor: pointer;
  background-color: #448e75;
  border: none;
  border-bottom-left-radius: 20%;
  width: 60px;
  height: 60px;
  font-size: 30px;
  transition: background-color .5s;
  position: absolute;
  right: 0;
  box-shadow: 0 0 10px #04040466;
}

#addToCartBtn.addToCartBtnClicked {
  background-color: #77747466;
}

#addToCartLabel {
  font-size: 25px;
  position: absolute;
  right: 60px;
}

#countCartItemsImage {
  background-color: #448e75;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  font-size: 25px;
  position: absolute;
  top: 62%;
  right: -22%;
}

.totalPrice {
  word-spacing: 200px;
  width: 450px;
  height: 25px;
  margin-bottom: 40px;
  margin-left: 30px;
  font-size: 2rem;
}

#checkout {
  cursor: pointer;
  background-color: #448e75;
  border: none;
  border-bottom-left-radius: 50px;
  width: 450px;
  height: 120px;
  font-size: 2rem;
}

